import React, {useEffect} from 'react'
import { useRouter } from 'next/router'


export default function Contact() {
       let fullUrl = "https://afaithraf.github.io/my-portfolio/";

    const router = useRouter();
    useEffect(() => {
        if(router.query['message'] === "sent"){
            alert("I've received your message, and I will reply as soon as possible.");
        }
    }, [])
    return (
        <div className="cont" id="contactP">

            <h2>Get in Touch</h2>
            
            <h2>Or</h2>
            <div className="box">
                <div className="left">
                    <div className="overlay">
                        <div className="part">
                            <h4>Address</h4>
                            <span>Bd Sidi Maafa,<br /> Oujda, 60000<br />Morocco</span>
                        </div>
                        <div className="part">
                            <h4>Email</h4>
                            <span>aworkraf@gmail.com</span><br />
                        </div>
                        <div className="part">
                            <h4>Phone/WhatsApp</h4>
                            <span>+212634-516342</span>
                        </div>
                    </div>
                </div>
                <div className="right">
                    <h2>Send Me A Message</h2>
                    <form action="https://mailthis.to/ashraf"
                        method="POST" encType="multipart/form-data" >

                        <input type="text" name="name" placeholder="Your name" />
                        <input type="email" name="_replyto" placeholder="Your email" />
                        <textarea name="message" placeholder="Enter your message here"></textarea>
                        <label style={{float: "left", margin: "0 52px"}}>Attachments (optional)</label>
                        <input type="file" name="file" placeholder="Attachments (optional)" />

                        <input type="hidden" name="_subject" value={"Contact form submitted at "+fullUrl } />
                        <input type="hidden" name="_after" value={fullUrl+ "?message=sent"} />
                        <input type="hidden" name="_honeypot" value="" />
                        <input type="hidden" name="_confirmation" value="I've received your message, and I will reply as soon as possible." />
                        <button type="submit" >Send</button>

                    </form>
                </div>
            </div>
            <style jsx>{`
            .cont {
                width: 100%;
                position: relative;
                background: #111111b3;
               backdrop-filter: blur(25px);
               text-align: center;
               color: #ffffff;
               display: flex;
               flex-direction: column;
               justify-content: center;
               padding: 20px 0;
            }
            .box{
                min-height: 80vh;
                width: 90vw;
                max-width: 1000px;
                background: #fff;
                margin: 30px auto;
                display: grid;
                grid-template-rows: 100%;
                grid-template-columns: 50% 50%;
            }
            @media (max-width: 900px){
                .box{
                    grid-template-columns: 100%;
                    grid-template-rows: 50% 50%;
                    height: 1111px;
                }
                .box .left .overlay{
                    text-align: center;
                }
            }
            .box .left{
                background: url("https://afaithraf.github.io/my-portfolio//assets/cbg.jpg");
                background-size: cover;
                padding: 0;
            }
            .overlay{
                background: #00000090;
                height: 100%;
                width: 100%;
                padding: 0;
                margin: 0;
                display: flex;
                flex-direction: column;
                text-align: left;
                justify-content: center;
            }
            .left h4{
                color: #fff;
                margin-top: 0;
                font-size: 1.2em;
            }
            .part{
                margin: 30px 80px;
                color: #e1e1e1;
            }
            .right{
                color: #010101;
                display: flex;
                flex-direction: column;
                height: 100%;
                justify-content: space-evenly;
            }
            .right h2{
            }
            .right input{
                height: 40px;
                width: 80%;
                margin: 10px;
                padding: 10px;
            }
            .right textarea{
                min-width: 80%;
                max-width: 80%;
                height: 200px;
                margin: 10px;
                padding: 10px;
            }
            .right button{
                background: #ffff00;
                border: 0;
                color: #010101;
                padding: 10px 20px;
                float: right;
                margin: 10px 40px;
                font-size: 1em;
                cursor: pointer;
            }
            `}</style>
        </div>
    )
}
